import { Col, Pagination, Row } from 'antd';
import { FC } from 'react';
import { Review } from '../../components';
import { useReviewContext } from '../../context/ReviewContext';
import './AppContent.less';

const AppContent: FC = () => {
    const { data, total, size, page, changePage, setDataSize } = useReviewContext();

    return (
        <div className="app-content">
            <Row gutter={[0, 20]}>
                <Col span={24}>
                    <div className="flex">
                        <Pagination
                            onChange={changePage}
                            onShowSizeChange={setDataSize}
                            current={page}
                            pageSize={size}
                            total={total}
                        />
                    </div>
                </Col>
                <Col span={24}>
                    <Row gutter={[20, 20]}>
                        {data.map((item) => (
                            <Col key={item.id} span={24}>
                                <Review item={item} />
                            </Col>
                        ))}
                    </Row>
                </Col>
            </Row>
        </div>
    );
};

export { AppContent };
