import { createContext, Dispatch, FC, SetStateAction, useCallback, useContext, useEffect, useState } from 'react';
import { createReview, deleteReview, editReview, getAllReviews } from '../api/reviews';
import { IReview } from '../models/Review';

interface IReviewContext {
    data: IReview[];
    total: number;
    page: number;
    size: number;
    setData: Dispatch<SetStateAction<IReview[]>>;
    setPage: Dispatch<SetStateAction<number>>;
    setSize: Dispatch<SetStateAction<number>>;
    setTotal: Dispatch<SetStateAction<number>>;
}

const ReviewContext = createContext<IReviewContext>({} as IReviewContext);

const ReviewContextProvider: FC = ({ children }) => {
    const [data, setData] = useState<IReview[]>([]);
    const [page, setPage] = useState<number>(1);
    const [size, setSize] = useState<number>(10);
    const [total, setTotal] = useState<number>(0);

    return (
        <ReviewContext.Provider
            value={{
                data,
                total,
                page,
                size,
                setData,
                setPage,
                setSize,
                setTotal,
            }}
        >
            {children}
        </ReviewContext.Provider>
    );
};

const useReviewContext = () => {
    const { data, total, page, size, setData, setPage, setSize, setTotal } = useContext(ReviewContext);

    const fetchData = useCallback(async () => {
        const { review, totalSize } = await getAllReviews(page - 1, size);
        setData(review);
        setTotal(totalSize);
    }, [page, setData, setTotal, size]);

    useEffect(() => {
        fetchData();
    }, [fetchData]);

    const changePage = useCallback((num: number) => setPage(num), [setPage]);

    const setDataSize = useCallback((num: number) => setSize(num), [setSize]);

    const createReviewAction = useCallback(
        async (review: Partial<IReview>) => {
            const newReview = await createReview(review);
            if (!!newReview) {
                setData((prev) => [newReview, ...prev]);
            }
        },
        [setData]
    );

    const editReviewAction = useCallback(
        async (review: Partial<IReview>) => {
            const editedReview = await editReview(review);
            if (!!editedReview) {
                setData((prev) =>
                    prev.map((item) => {
                        if (item.id === editedReview.id) {
                            return editedReview;
                        } else {
                            return item;
                        }
                    })
                );
            }
        },
        [setData]
    );

    const deleteReviewAction = useCallback(
        async (id: string) => {
            const success = await deleteReview(id);
            if (success) {
                setData((prev) => prev.filter((item) => item.id !== id));
            }
        },
        [setData]
    );

    return {
        data,
        total,
        page,
        size,
        changePage,
        setDataSize,
        createReviewAction,
        editReviewAction,
        deleteReviewAction,
    };
};

export default ReviewContextProvider;

export { useReviewContext };
